.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  background-color: #e6e6f8;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.input-search {
  width: 500px;
  border: 1px solid #c2c5f9;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-weight: bold;
}

.btn {
  cursor: pointer;
  color: #f4f4fd;
  background-color: #89898e;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-weight: bold;
  display: block;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.gallery {
  flex-wrap: wrap;
  gap: 50px 10px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 7.5px);
}

img {
  width: 100%;
  height: 80%;
  object-fit: cover;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  display: block;
}

.info {
  max-height: 20%;
  text-align: center;
  column-gap: 10px;
  display: flex;
}

